footer {
    padding: 2vmax;
    background-color: rgb(34, 33, 33);
    color: white;
    display: flex;
    align-items: center;
    bottom: 0;
    width: 100%;
  }
  
  .leftFooter,
  .rightFooter {
    flex-basis: 20%;
    text-align: center;
  }
  
  .leftFooter > h4,
  .rightFooter > h4 {
    font-family: "Roboto";
    font-size: 1.4vmax;
    text-decoration: underline;
  }
  
  .leftFooter > p {
    text-align: center;
    font-size: 1.2vmax;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  }
  
  .leftFooter > img {
    width: 10vmax;
    margin: 1vmax;
    cursor: pointer;
  }
  
  .midFooter {
    flex-basis: 60%;
    text-align: center;
  }
  
  .midFooter > h2 {
    font-size: 4vmax;
    font-family: "Roboto";
    color: var(--main-text-green);
  }
  
  .midFooter > p {

    max-width: 60%;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    margin: 1vmax auto;
  }
  h4{
    padding-bottom: 1rem;
  }
  .rightFooter > a {
    text-decoration: none;
    font-size: 1.3vmax;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    color: white;
    transition: all 0.5s;
    margin: 0.5vmax;
    display:inline;
  }
  
  .rightFooter > a:hover {
    color: #b8ff9d;
  }
  
  @media (max-width: 768px) {
    footer {
      flex-wrap: wrap;
      justify-content: space-around;
      padding: 2vmax 0;
    }
  
    .leftFooter,
    .midFooter,
    .rightFooter {
      flex-basis: 100%;
      text-align: center;
      margin-bottom: 1.5vmax;
    }
    .midFooter > h2 {
        font-size: 3.5vmax;
      }
      
  }
  
  